import * as XLSX from 'xlsx';
import { loadWAFData } from "../../../utils/helpers";
import { fetchCsrfToken } from "../PlantAssignment/fetchCsrfService";

const WAFData = await loadWAFData();
export const uploadhysicalProduct = async (file, background) => {
    let physicalProductData;
    let items=[];
    let createPartUrl = process.env.REACT_APP_CREATE_PART_URL;
    console.log("createPartUrl", createPartUrl);
    // Handle file change and read the Excel file
        if (!file) return;
        const reader = new FileReader();
        reader.onload = (event) => {
            const binaryString = event.target.result;
            const workbook = XLSX.read(binaryString, { type: 'array' });
            // Get the first sheet
            const sheetName = workbook.SheetNames[0];
            const sheet = workbook.Sheets[sheetName];
            // Convert the sheet to JSON
            const physicalProductJSONData = XLSX.utils.sheet_to_json(sheet);
            console.log(JSON.stringify(physicalProductData));
            physicalProductJSONData.forEach((data) => {
                console.log("data", data);
                items.push({
                    "type": "VPMReference",
                    "attributes": {
                        "title": data.title,
                        "description": data.description,
                        "dseno:EnterpriseAttributes": {
                            "XP_VPMReference_Ext.EMR_ERP_PrimaryUOM": data['Unit of Measure'],
                            "XP_VPMReference_Ext.ISV_ItemCatalogCategory": data['Item Catalog Category'],
                        }
                    }
                })
            });
        };
        reader.readAsArrayBuffer(file);
        //Reading physicalProductData
        console.log("Items----"+JSON.stringify(items));
        

    if (background) {
        //Need to call node web service
        return new Promise((resolve, reject) => {
            resolve("Success");
        });
    }
    else {
        const headers=await fetchCsrfToken();
        console.log("Headers", headers);
        return new Promise((resolve, reject) =>{
            WAFData.authenticatedRequest("https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem", {
                method: "POST",
                headers: headers,
                type: "json",
                data: JSON.stringify(items),
                onComplete: (response) => {
                    console.log("Received response:", response);
                    resolve(response); // Resolve the promise with the response data
                },
                onFailure: (error) => {
                    console.error("Request failed:", error);
                    reject(error); // Reject the promise with the error
                },
            });
        }
        )
    }
};
