import useToast from "../../hooks/useToast";
import { loadWAFData } from "../../utils/helpers";
import api from "../../utils/api";

export const initializeDroppableArea = (droppableContainer, handleDrop) => {
  // const { showErrorToast } = useToast();
  window.require(["DS/DataDragAndDrop/DataDragAndDrop"], (DataDragAndDrop) => {
    DataDragAndDrop.droppable(droppableContainer, {
      drop: (data) => {
        console.log("[DragAndDrop] Drop event:", data);
        const parsedData = JSON.parse(data);

        let objList = parsedData.data.items;
        let objsLength = objList.length;
        let ProductType = objList[0].objectType;
        console.log("objectType---->", ProductType);
        console.log("objList---->", objList);
        if (objsLength > 1) {
          alert("Please drop only one Product."); // we need tosters here

          return;
        }

        if ("VPMReference" !== ProductType && "Raw_Material" !== ProductType) {
          alert("Please drop only Physical Products or Raw Materials"); // toasters
          return;
        }
        handleDrop(parsedData.data.items);
        droppableContainer.classList.remove("drag-over");
      },
      enter: () => {
        droppableContainer.classList.add("drag-over");
      },
      leave: () => {
        droppableContainer.classList.remove("drag-over");
      },
    });
  });
};

